import React from 'react'
import { toast } from "react-toastify";
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader'
import cssVar from 'views/mobileView/styles'
import db from 'libs/db'
import AdminServices from 'services/api/AdminServices';
import { BsGraphUp } from 'react-icons/bs';
import Modal from 'react-bootstrap/Modal';
import { AiOutlineRight } from 'react-icons/ai';
import PerformanceChart from 'views/mobileView/components/molecules/Cards/PerformanceChart';

const NameMintbox = (props) => {

    const [mintboxName, setMintboxName] = React.useState()
    const [selectedETFs, setSelectedETFs] = React.useState([])
    const [rebalanceFrequency, setRebalanceFrequency] = React.useState('')

    const [user, setUser] = React.useState({});
    const [showHistoricalModal, setShowHistoricalModal] = React.useState(false)

    const minInv = props.location.state.minInv
    React.useEffect(async () => {
        await loadData();
    }, []);

    const loadData = async () => {
        try {
            await db.get(db.model.user).then((p) => {
                setUser(p.userData);
                console.log("user: ", p.userData)
            });
            await db.get(db.model.createMintbox).then((u) => {
                if (u?.selectedETFs) {
                    setSelectedETFs(u.selectedETFs);
                }
                if (u?.mintboxName) {
                    setMintboxName(u.mintboxName || '')
                }
                if (u?.rebalanceFrequency) {
                    setRebalanceFrequency(u.rebalanceFrequency)
                }
            })
        } catch (error) {
            toast.error(error);
        }
    };
    const saveData = async (name) => {
        const dataFinal = {
            mintboxName: name
        };
        await db.get(db.model.createMintbox).then((u) => {
            if (u) { Object.assign(u, dataFinal) }
            else { u = dataFinal }
            db.set(db.model.createMintbox,JSON.stringify(u));
        })
    }
    
    const onNextPress = async ({type}) => {
        if (mintboxName?.length < 2) {
            return
        }
        saveData(mintboxName)
        const x = selectedETFs.map((fund) => {
            return {
                fundCode: fund.Symbol,
                indexId: fund.IndexId,
                assetId: fund.MintboxAssetId,
                weightage: Number(fund.allocation),
                CurrentWeightage: Number(fund.allocation)
            }
        })
        const final = {
            clientId: user.id,
            name: mintboxName,
            description: 'My custom mintbox',
            risk: 1,
            duration: 1,
            minInvestment: minInv,
            xirr: 0,
            constituents: x,
            rebalanceFrequency: rebalanceFrequency,
            videoLink: "",
            fee: "0"
        }
        console.log("CREATE MINTBOX API", final)
        let res = await AdminServices.createMintbox(final).then((res) => JSON.stringify(res));
        res = await JSON.parse(res);
        if (res.type === true) {
            if (res.data.Result === 'Success') {
                console.log("Mintbox created", res.data)
                if(type === 2){
                    props.history.push({
                        pathname: '/portfolio/confirm-invest',
                        state: {
                          fundData: { MintboxId: res.data.MintboxId, MintboxName: mintboxName,RiskLevel:1, MinSIPAmount: minInv, Constituents:x},
                          type: 3
                        }
                    })
                } else if(type===1){
                    console.log(res.data)
                    const data = {
                        "clientId": user.id,
                        "fundType": 3,
                        "fundCode": res.data.MintboxId+'',
                        "isActive": 1
                    }
                    let res2 = await AdminServices.updateWatchlist(data).then(res => res)
                    if (res2.type) {
                        toast.success("You are now tracking this mintbox")
                        props.history.push('/dashboard')
                    }
                }
            }
            else {
                toast.error(res.data)
            }
        } else {
            toast.error()
        }
    }
    const Footer = () => {
        return (
            <>  
                <div style={{ position: "fixed", bottom: '70px', width: "100%",backgroundColor: cssVar.getPropertyValue('--white'), textAlign: "center" }}>
                    <div className="bodyTextDark secondaryGreen" onClick={() => setShowHistoricalModal(true)} style={{ borderTop: "1px solid "+cssVar.getPropertyValue('--light'), padding: "3% 4%"}}>
                        Historical performance <BsGraphUp size={14} style={{ margin: "5px" }} />
                    </div>
                    <div className="bodyTextLight" style={{ borderTop: "1px solid "+cssVar.getPropertyValue('--light'), padding: "2% 4%"}}>
                        Min investment <b style={{ color: "#333333" }}>‚Çπ {Number(minInv).toFixed(2)}</b>
                    </div>
                </div>
                <div className='footer' style={{ gap: '4%' }}>
                    <div style={{ flex: 1}}>
                        <div className='bodyTextDark'>
                            Track this mintbox
                        </div>
                        <div className="header3dark secondaryGreen"
                            style={{opacity: mintboxName?.length >= 2 ? 1 : 0.4 }}
                            onClick={()=>onNextPress({type:1})}>
                            Add to watchlist <AiOutlineRight size={10} style={{marginTop:"1%"}}/>
                        </div>
                    </div>
                    <div style={{ flex: 1 }}>
                        <button
                            className='solidButton'
                            style={{ opacity: mintboxName?.length >= 2 ? 1 : 0.4 }}
                            onClick={() => { onNextPress({type:2}) }}
                        >
                            Invest Now
                        </button>
                    </div>
                </div>
            </>
        )
    }

    const HistoricalModal = () => {
        return (
            <Modal
                className="bottom-drawer"
                show={showHistoricalModal}
                onHide={() => setShowHistoricalModal(false)}
                keyboard={false}
            >
                <Modal.Body style={{ maxHeight: "80vh", overflow: "scroll",padding:"0" }}>
                    {/* <SipLumpChart etfs={selectedETFs} type={4} fundCode={1} /> */}
                    <PerformanceChart etfs={selectedETFs} type={4} fundCode={1} rebalanceFreq={rebalanceFrequency===1?'Q':rebalanceFrequency===2? '6M': rebalanceFrequency === 3? 'Y': rebalanceFrequency===4? false: 'Q'}/>
                </Modal.Body>
                <Modal.Footer>
                    <button className="outlineButton" onClick={() => setShowHistoricalModal(false)}>
                        Close
                    </button>
                </Modal.Footer>
            </Modal>
        )
    }

    return (
        <div className='mainContainer'>
            <MainHeader
                header="Build your own mintbox"
                navigation={props.history}
            />
            <div className="dataContainer" style={{ paddingTop:"2%" }}>
                <div className='header1dark' style={{marginTop:"4%"}}>Woohoo! You just built a mintbox ü•Å</div>
                <div className='bodyTextDark' style={{ marginTop: "4%", fontSize:"16px"}}>What would you like to call it?</div>
                <div style={{ marginTop: "6%" }}>
                    <input
                        className="normalInput"
                        type="text" 
                        maxLength={20}
                        placeholder="My wealth box"
                        id="Full-name"
                        value={mintboxName}
                        onChange={e => setMintboxName(e.target.value)} 
                    />
                </div>
            </div>
            <HistoricalModal />
            <Footer />
        </div>
    )
}

export default NameMintbox
